import { CFormGroup } from "@coreui/react";
import { useState } from "react";
import Creatable from "react-select/creatable";

const SuspectIpAddress = ({ getValue, defaultValue }) => {
  const [value, setValue] = useState(defaultValue ?? []);

  const handleCreateEmail = (v) => {
    let d = { label: v, value: v };
    setValue([...value, d]);
    getValue([...value, d]);
  };

  const handleChangeEmail = (v, actionMeta) => {
    if (v.length === 0) {
      setValue([]);
      getValue([]);
    } else {
      const f = value.filter((s) => s !== actionMeta.removedValue);
      setValue(f);
      getValue(f);
    }
  };

  return (
    <CFormGroup>
      <label>Suspect IP Address</label>
      <Creatable isMulti isClearable onCreateOption={handleCreateEmail} value={value} onChange={handleChangeEmail} />
    </CFormGroup>
  );
};

export default SuspectIpAddress;
