import { useState } from "react";
import CaseInformation from "../fields/CaseInformation";
import CTNumber from "../fields/CTNumber";
import DateMadeAvailable from "../fields/DateMadeAvailable";
import IncidentDate from "../fields/IncidentDate";
import Notes from "../fields/Notes";
import Priority from "../fields/Priority";
import ReportingEsp from "../fields/ReportingEsp";
import SuspectDOB from "../fields/SuspectDOB";
import SuspectEmail from "../fields/SuspectEmail";
import SuspectIpAddress from "../fields/SuspectIpAddress";
import SuspectPhone from "../fields/SuspectPhone";
import SuspectScreenName from "../fields/SuspectScreenName";

const FormText = ({ getData, defaultData }) => {
  const x = {
    ctNumber: "",
    priority: "",
    reportingEsp: "",
    suspectEmails: [],
    suspectScreenName: "",
    suspectIpAddress: [],
    dateMadeAvailable: "",
    notes: "",
    caseInformation: "",
    suspectPhone: "",
    suspectDob: "",
    incidentDate: "",
  };
  const [data, setData] = useState(defaultData ?? x);
  const handleChange = (field, v) => {
    data[field] = v;
    setData(data);
    getData(data);
  };

  return (
    <div className="row">
      <div className="col-md-6">
        <CTNumber getValue={(e) => handleChange("ctNumber", e)} defaultValue={data.ctNumber} />
        <Priority getValue={(e) => handleChange("priority", e)} defaultValue={data.priority} />
        <ReportingEsp getValue={(e) => handleChange("reportingEsp", e)} defaultValue={data.reportingEsp} />
        <SuspectEmail getValue={(e) => handleChange("suspectEmails", e)} defaultValue={data.suspectEmails} />
        <SuspectPhone getValue={(e) => handleChange("suspectPhone", e)} defaultValue={data.suspectPhone} />
        <SuspectDOB getValue={(e) => handleChange("suspectDob", e)} defaultValue={data.suspectDob} />
        <SuspectScreenName getValue={(e) => handleChange("suspectScreenName", e)} defaultValue={data.suspectScreenName} />
      </div>
      <div className="col-md-6">
        <DateMadeAvailable getValue={(e) => handleChange("dateMadeAvailable", e)} defaultValue={data.dateMadeAvailable} />
        <IncidentDate getValue={(e) => handleChange("incidentDate", e)} defaultValue={data.incidentDate} />
        <SuspectIpAddress getValue={(e) => handleChange("suspectIpAddress", e)} defaultValue={data.suspectIpAddress} />
        <CaseInformation getValue={(e) => handleChange("caseInformation", e)} defaultValue={data.caseInformation} />
        <Notes getValue={(e) => handleChange("notes", e)} defaultValue={data.notes} />
      </div>
    </div>
  );
};

export default FormText;
