import { CFormGroup } from "@coreui/react";
import { useState } from "react";

const Priority = ({ getValue, defaultValue }) => {
  const priorities = ["1", "2", "E"];
  const [value, setValue] = useState(defaultValue ?? "");

  const handleChange = (e) => {
    setValue(e.target.value);
    getValue(e.target.value);
  };

  return (
    <CFormGroup>
      <label>Priority</label>
      <div>
        {priorities.map((p) => (
          <div className="form-check form-check-inline" key={p}>
            <input className="form-check-input" type="radio" onChange={handleChange} value={p} checked={p === value} />
            <label className="form-check-label">{p}</label>
          </div>
        ))}
      </div>
    </CFormGroup>
  );
};

export default Priority;
