import { CFormGroup } from "@coreui/react";
import { useState } from "react";

const DateMadeAvailable = ({ getValue, defaultValue }) => {
  const [value, setValue] = useState(defaultValue ?? "");

  const maxDate = () => {
    const d = new Date();
    const m = d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
    const date = d.getDate() + 1 < 10 ? "0" + d.getDate() : d.getDate();
    return `${d.getFullYear()}-${m}-${date}`;
  };

  const handleChange = (e) => {
    setValue(e.target.value);
    getValue(e.target.value);
  };

  return (
    <CFormGroup>
      <label>Date Made Available</label>
      <input type="date" className="form-control" max={maxDate()} value={value} onChange={handleChange} />
    </CFormGroup>
  );
};

export default DateMadeAvailable;
